.dialogue_list {
  display: block;
  width: 30%;
  height: 100%;
  overflow: auto;
}

.dialogue_list input {
  border: none;
  width: 100%;
  border-bottom: 1px solid #d7d8db;
  outline: none;
  padding: 10px;
}

a {
  text-decoration: none;
  color: black;
}

.dialogue_item {
  display: flex;
  flex-direction: column;
  justify-content: center;

  text-align: center;
  width: 100%;
  height: 63px;
  border-bottom: 1px solid #d7d8db;
}

.dialogue_item:hover {
  background: rgba(81, 129, 184, 0.1);
}

a.active {
  font-size: 20px;
  font-weight: bold;
}
