.messages {
  display: flex;
  flex-direction: column;
  width: 70%;
  height: 100%;
  border-left: 1px solid #d7d8db;
}

.smsBlock {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  overflow: auto;
}

.sms {
  display: inline-block;
  background: #d7d8db;
  border-radius: 10px;
  float: right;
  margin: 10px;
  height: auto;
  width: auto;
  max-width: 250px;
  padding: 5px 10px;
  word-wrap: break-word;
}

.input_block {
  display: flex;
  align-items: center;
  height: 70px;
  width: 100%;
  justify-content: center;
  border-top: 1px solid #d7d8db;
  background: rgba(250, 251, 252, 1);
}

.input_block input {
  width: 80%;
  height: 30px;
  padding: 15px;
  background: white;
  border: 1px solid #d7d8db;
  border-radius: 5px;
  margin: 0 10px;
}

.input_block button {
  background: #d7d8db;
  border: none;
  border-radius: 5px;
  padding: 15px;
  cursor: pointer;
}
